   s_progress            = new EventEmitter<any>();
   s_reachBeginning      = new EventEmitter<any>();
   s_reachEnd            = new EventEmitter<any>();
   s_destroy             = new EventEmitter<any>();
   s_setTranslate        = new EventEmitter<any>();
   s_setTransition       = new EventEmitter<any>();
   s_autoplay            = new EventEmitter<any>();
   s_autoplayStart       = new EventEmitter<any>();
   s_autoplayStop        = new EventEmitter<any>();
   s_lazyImageLoad       = new EventEmitter<any>();
   s_lazyImageReady      = new EventEmitter<any>();
   s_paginationRendered  = new EventEmitter<any>();
   s_scroll              = new EventEmitter<any>();

  constructor(private zone: NgZone, private elementRef: ElementRef, private differs : KeyValueDiffers,
     private defaults: SwiperConfig) {}

  ngOnInit() {
    let element = this.elementRef.nativeElement;
    let options = new SwiperConfig(this.defaults);

    options.assign(this.config); // Custom config

    if (this.runInsideAngular) {
      this.swiper = new Swiper(element, options);
    } else {
      this.zone.runOutsideAngular(() => {
        this.swiper = new Swiper(element, options);
      });
    }

    // Add native swiper event handling
    SwiperEvents.forEach((eventName) => {
      let self = this;

      this.swiper.on(eventName, function(event) {
        self[`s_${eventName}`].emit(event);
      });
    });

    if (!this.configDiff) {
      this.configDiff = this.differs.find(this.config || {}).create(null);
    }
  }

  ngDoCheck() {
    let changes = this.configDiff.diff(this.config || {});

    if (changes) {
      this.ngOnDestroy();

      // This is needed for the styles to update properly
      setTimeout(() => {
        this.ngOnInit();

        this.update();
      }, 0);
    }
  }

  ngOnDestroy() {
    if (this.swiper) {
      if (this.runInsideAngular) {
        this.swiper.destroy(true, true);
      } else {
        this.zone.runOutsideAngular(() => {
          this.swiper.destroy(true, true);
        });
      }

      this.swiper = null;
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.swiper && changes['disabled']) {
      if (changes['disabled'].currentValue != changes['disabled'].previousValue) {
        if (changes['disabled'].currentValue === true) {
          this.swiper.lockSwipes();
        } else if (changes['disabled'].currentValue === false) {
          this.swiper.unlockSwipes();
        }
      }
    }
  }

